import Img from 'gatsby-image'
import React from 'react'
import Emoji from 'react-emoji-render'
import styled from 'react-emotion'
import { borders, shadows } from '../theme'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
}

const StyledArtisanCard = styled.div`
  background: #fff;
  min-width: 300px;
  box-shadow: ${shadows.bothDiffused};
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 4px;

  .picture {
    position: relative;
    width: 100%;
    height: 170px;
    overflow: hidden;
    align-self: center;
    border-radius: ${borders.radius.small};
  }

  .name {
    font-weight: bold;
    font-size: 1.3rem;
    color: #4a4a4a;
    margin-top: 20px;
    margin-bottom: 7px;
  }

  .type {
    font-size: 1.2rem;
    color: #9b9b9b;
    margin-bottom: 10px;
  }

  .description {
    font-size: 1.2rem;
    color: #4a4a4a;
  }

  @media (max-width: 768px) {
    min-width: 260px;
  }
`

const ArtisanCard = ({ artisan, picture, type, children }) => (
  <StyledArtisanCard>
    <div className="picture">
      <Img sizes={picture.sizes} alt={picture.title} style={imgStyle} />
    </div>
    <p className="name">{artisan}</p>
    <Emoji className="type" text={type} />
    <p className="description">{children}</p>
  </StyledArtisanCard>
)

export default ArtisanCard
export { StyledArtisanCard }



